import React from "react";
import Icon from "@ezycore/ui/components/atoms/Icon";
import { IconProps } from "@ezycore/ui/components/atoms/Icon";

export interface InputProps {
  placeholder: string | null;
  name: string;
  icon: IconProps;
}

function Input({ placeholder = null, name, icon, label }: InputProps) {
  return (
    <div className="input">
      <div className="input__inner">
        {label && (
          <label className="input__label" htmlFor="">
            {label}
          </label>
        )}
        <input
          type="text"
          name={name}
          placeholder={`${placeholder || "Im an input text"}`}
        />
        {icon && (
          <div className="input__icon">
            <Icon icon={icon} />
          </div>
        )}
      </div>
    </div>
  );
}

export default Input;
